import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { BreezeBridgeAngularModule } from 'breeze-bridge-angular'
import { DropdownModule, DataTableModule } from 'primeng/primeng';

import { APP_CONFIG, DevAppConfig } from './app.config';

import { AppComponent } from './app.component';
import { routing }        from './app.routing';

import { AuthGuard } from './_guards/index';
import { AuthenticationService, LoginComponent, LoginHelpComponent, RegisterComponent, 
         ChangePasswordComponent, ForgotPasswordComponent,
         ResetPasswordComponent } from './_authentication/index';
             
import { HomeComponent } from './home/index'; 

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ToastModule } from 'ng2-toastr/ng2-toastr';

import { DataService, BusyService, NotifyService, ScriptService } from './_services/index';

import { TransportOrdersListComponent, TransportOrderComponent, 
         GatetransOfTrasporderComponent } from './transport-orders/index';         

import { Faq } from './faq/faq.component';
import { Guidelines } from './guidelines/guidelines.component';

import { UserInfoComponent } from './userinfo/userinfo.component'; 

import { FileHelper } from './_helpers/index';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    LoginHelpComponent,
    RegisterComponent,
    UserInfoComponent,
    ChangePasswordComponent,
    ForgotPasswordComponent,
    ResetPasswordComponent,
    HomeComponent,
    TransportOrdersListComponent,
    TransportOrderComponent,
    GatetransOfTrasporderComponent,
    Faq,
    Guidelines
  ],
  imports: [
    DropdownModule,
    DataTableModule,
    BreezeBridgeAngularModule,
    BrowserModule,
    FormsModule,
    HttpModule,
    routing,
    BrowserAnimationsModule,
    ToastModule.forRoot()
  ],
  providers: [    
    { provide: APP_CONFIG, useValue: DevAppConfig },    
    AuthGuard,
    AuthenticationService,
    DataService,
    BusyService,
    NotifyService,
    ScriptService
],
  bootstrap: [AppComponent]
})
export class AppModule { }



