import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FileRead, DataService, TransOrdStatuses, NotifyService } from '../_services/index';
import { Transport_Orders, Material_Types, Customers, Transport_Orders_Status_Values 
       } from '../_entities/entity-model';
import * as FileSaver from "file-saver";
import { SelectItem } from 'primeng/primeng';
import { FileHelper } from '../_helpers/index';



export enum Datastates { Insert, Edit, View};

@Component({
    moduleId: module.id,
    templateUrl: 'transport-order.component.html'
})
export class TransportOrderComponent implements OnInit, OnDestroy {
     
    _selectFromList = "<Επιλέξτε απο την λίστα>";
    loading = false;
    title = "Εντολή Μεταφοράς";
    errorString : string;

    TransOrdStatuses = TransOrdStatuses;
    Datastates = Datastates;
    datastate : Datastates;

    data : Transport_Orders = new Transport_Orders();
    //data : any = {};
    materials : SelectItem[]; // for Combobox
    transporters : SelectItem[]; // for Combobox
    statuses : SelectItem[]; // for Combobox
    

    constructor(private router: Router,
                private route: ActivatedRoute,
                private notify: NotifyService,
                private ds: DataService
    ) { }

    ngOnInit() {
        
        this.route.params
            .subscribe(params => {
                const TransportOrderGUID = params['id'];
                if (TransportOrderGUID == '0') {
                    // New Order
                    this.datastate = Datastates.Insert;
                    this.title = "Καταχώρηση Νέας Εντολής Μεταφοράς";
                    this.data = new Transport_Orders(); // Χρειάζεται ?
                }
                else
                {
                    this.datastate = Datastates.View;
                    this.title = "Εντολή Μεταφοράς (Παρουσίαση Μόνο)";
                    this.ds.getTransportOrderViewByGUID(TransportOrderGUID)
                           .then(v => {
                            this.data = v;
                           });
                    
                }
                return TransportOrderGUID;
            });
        this.getLookups();
    }

    ngOnDestroy(): void {
        if (this.datastate == Datastates.Insert) {
            this.notify.error('ΑΚΥΡΩΘΗΚΕ η Καταχώρηση Νέας Εντολής Μεταφοράς');
        }        
    }

    getLookups() {
        this.ds.getMaterials()
             .then((v) => this.materials = v.map(v => ({label: v.Name, value: v.NID})));
        this.ds.getTransporters()
            .then((v) => this.transporters = v.map(v => ({label: v.Name, value: v.IID})));
        this.ds.getStatuses()
            .then((v) => this.statuses = v.map(v => ({label: v.Name, value: v.Id})));
    
    }   



    file: File;
    fileRead : FileRead = new FileRead();
    
    fileLoadingTextOut: string = '';
    fileIsLoading : boolean = false;
    onFileAttach(event) {
        let eventObj: MSInputMethodContext = <MSInputMethodContext> event;
        let target: HTMLInputElement = <HTMLInputElement> eventObj.target;
        let files: FileList = target.files;
        this.file = files[0];
        this.fileRead.fileName = this.file.name;

        // Load File
        var reader = new FileReader();        
        reader.onloadend = file => {
                var contents: any = file.target;
                
                this.fileRead.fileData = contents.result;  // text/plain;base64,xxxxx
                this.fileRead.fileType = this.fileRead.fileData.substring(0, this.fileRead.fileData.indexOf(";"));
                this.fileRead.fileData = this.fileRead.fileData.substring( this.fileRead.fileData.indexOf("base64,")+7);
                this.fileLoadingTextOut = 'το αρχείο είναι έτοιμο για ανέβασμα';
        };
        this.fileLoadingTextOut = '...loading';        
        reader.readAsDataURL(this.file);
    }
    openFile(guid:string){

        this.ds.getFileRepositoryByGUID(guid)
                .then(rec => {

                    let mediaType = FileHelper.getContentType(rec.FileName);
                    //console.log(rec.FileData);                
    
                    let fileName = rec.FileName;
    
                    // convert base64/URLEncoded data component to raw binary data held in a string
                    var byteString;
                    byteString = atob(rec.FileContent);
                    // write the bytes of the string to a typed array
                    var ia = new Uint8Array(byteString.length);
                    for (var i = 0; i < byteString.length; i++) {
                        ia[i] = byteString.charCodeAt(i);
                    }
    
                    let blob = new Blob([ia], {type:mediaType});                
                    FileSaver.saveAs(blob, fileName);
                    
                }, (rejected) => {
                    console.log(rejected);
                });
    }

    save() {

        let hasErrors = false;
        this.errorString = '';
        if (!this.fileLoadingTextOut) {
            hasErrors = true;
            this.notify.error("Πρέπει να δώσετε Αρχείο Αίτησης");
            this.errorString = this.errorString.concat("Πρέπει να δώσετε Αρχείο Αίτησης. ");
        }
        if ((this.data.NoOfPassages < 1) || (this.data.NoOfPassages > 50)) {
            hasErrors = true;
            this.notify.error("Πρέπει το Πλήθος Διελεύσεων να είναι απο 1 έως 50");
            this.errorString = this.errorString.concat("Πρέπει το Πλήθος Διελεύσεων να είναι απο 1 έως 50. ");
        }
        if (hasErrors) {
            return;
        }

        this.ds.addNewTransportOrder(this.data, this.fileRead)
            .then(success => {                
                this.notify.success("Η Εντολή Αποθηκεύτηκε");
                this.datastate = Datastates.View;
                this.loading = false;
                this.router.navigate(['/listofapplications']);                
            },
            error => {
                this.loading = false;
                this.notify.error(error);
            });
    }


}



