import { DataService } from './_services/data.service';
import { environment } from '../environments/environment';
import { enableProdMode, Component, OnInit, ViewContainerRef, Inject } from '@angular/core';
import { AuthenticationService } from './_authentication/index';
import { ToastsManager, Toast } from 'ng2-toastr/ng2-toastr';
import { APP_CONFIG, IAppConfig} from './app.config';
import { BusyService, NotifyService } from './_services/index';


if (environment.production) {
    enableProdMode();
}                

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'Εντολές Μεταφορών Δήμων';
  version = "";

  

  constructor(public authService:AuthenticationService, 
              private toastr: ToastsManager, vRef: ViewContainerRef,
              private notify: NotifyService,
              @Inject(APP_CONFIG) private config,
              public busyService: BusyService,
              public ds: DataService
              ) {
    this.toastr.setRootViewContainerRef(vRef);    
  }
  ngOnInit() {
      // get users from secure api end point
      // this.userService.getUsers()
      //     .subscribe(users => {
      //         this.users = users;
      //     });
      this.version = this.config.version;     
  }
  
}



