import { ScriptService } from './../_services/script.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LazyLoadEvent, SelectItem } from 'primeng/primeng';
import { DataService, NotifyService, BusyService } from '../_services/index';
import { Transport_Orders_View } from '../_entities/entity-model';
import * as FileSaver from "file-saver";
//import * as pdfMake from 'pdfmake';
declare var pdfMake: any;


class Filters {
    TransporterIID : number; MaterialIID:number;
    StatusId: number; TransportOrderCodeId: number;
}
@Component({
    moduleId: module.id,
    templateUrl: 'transport-orders-list.component.html',
    styleUrls: ['transport-orders-list.component.css']
})
export class TransportOrdersListComponent implements OnInit {
     
    data : Transport_Orders_View[];
    totalRecords: number;
    firstRecord: number;
    rowsPerPage: number;

    _selectFromList = "<Επιλέξτε απο την λίστα>";
    filters : Filters = new Filters();
    materials : SelectItem[]; // for Combobox
    transporters : SelectItem[]; // for Combobox
    statuses : SelectItem[]; // for Combobox

    constructor(private router: Router,
                private notify: NotifyService,
                private ds: DataService,
                private busyService : BusyService,
                private scriptService : ScriptService

    ) { }

    ngOnInit() {
        this.totalRecords = 0;
        this.getLookups();
    }
    
    onSelect(id:string): void {
        this.router.navigate(['/application', id]); 
    }

    lazyLoad(event: LazyLoadEvent) {
        //in a real application, make a remote request to load data using state metadata from event
        //event.first = First row offset
        //event.rows = Number of rows per page
        //event.sortField = Field name to sort with
        //event.sortOrder = Sort order as number, 1 for asc and -1 for dec
        //filters: FilterMetadata object having field as key and filter value, filter matchMode as value
        /*
        this.ds.getTransportOrdersView(event.first, event.rows)
            .then(v => {
                this.totalRecords = this.ds.transportOrdersViewCount;
                this.data = v
            });
        */
        /*
        this.ds.getTransportOrdersView(this.firstRecord, this.rowsPerPage)
        .then(v => {
           this.totalRecords = this.ds.transportOrdersViewCount;
           this.data = v
        });
        */
        this.ds.getTransportOrdersViewFiltered(this.firstRecord, this.rowsPerPage,
                    this.filters.TransporterIID, this.filters.MaterialIID, 
                    this.filters.StatusId, this.filters.TransportOrderCodeId)
            .then(v => {
                this.totalRecords = this.ds.transportOrdersViewCount;
                this.data = v
            });
    
   
    } 

    applyFilters() {
        this.lazyLoad(null);
    }     
    clearFilters() {
        this.filters = new Filters();
        this.applyFilters();
    }     

    getLookups() {
        this.ds.getMaterials()
             .then((v) => this.materials = v.map(v => ({label: v.Name, value: v.NID})));
        this.ds.getTransporters()
            .then((v) => this.transporters = v.map(v => ({label: v.Name, value: v.IID})));
        this.ds.getStatuses()
            .then((v) => this.statuses = v.map(v => ({label: v.Name, value: v.Id})));
    
    }    

    getAllTransportOrdersView():Promise<Transport_Orders_View[]> {
        // fetch all
        return this.ds.getTransportOrdersViewFiltered(0, 1000,
            this.filters.TransporterIID, this.filters.MaterialIID, 
            this.filters.StatusId, this.filters.TransportOrderCodeId)
        
    }

    exportToPdf(){
        this.scriptService.load('pdfmake').then(data => {
            console.log('script loaded ', data); 
            this.scriptService.load('vfs_fonts').then(data => {
                console.log('script loaded ', data); 
                
                // fetch all
                this.getAllTransportOrdersView().then(v => {
                    // build csv
                    this.busyService.busy(this.buildPdf(v));
                });  
            });  


        }).catch(error => console.log(error));

    }

    buildPdf(data: Transport_Orders_View[]):Promise<boolean> {

        // prepare the document definition using declarative approach
        let docDefinition: any  = {
            // a string or { width: number, height: number }
            pageSize: 'A4',

            // by default we use portrait, you can change it to landscape if you wish
            pageOrientation: 'landscape',

            // [left, top, right, bottom] or [horizontal, vertical] or just a number for equal margins
            pageMargins: [40, 60, 40, 50],

            //header: { text: 'Χ.Υ.Τ.Α. Μαυροράχης - Φο.Δ.Σ.Α. Κεντρικής Μακεδονίας', style: 'pageHeader', margin: [40, 20, 40, 20] },
            header: { 
                columns:[
                    {
                    text: 'Χ.Υ.Τ.Α. Μαυροράχης - Φο.Δ.Σ.Α. Κεντρικής Μακεδονίας' 
                    
                    },
                    {
                        text: 'Δήμος ',
                        alignment: 'right'
                    }
                ],
                style: 'pageHeader', 
                margin: [40, 20, 40, 20] 
            },

            footer: function(currentPage, pageCount) {
                return {
                    text: 'Σελίδα ' + currentPage.toString() + ' από ' + pageCount.toString() + ' ', alignment: 'right', style: 'pageFooter',
                    margin: [40, 0, 40, 0]
                };
            },
            content: [
              {
                  text: 'Εντολές Μεταφοράς', style: 'title', alignment: 'center', margin: [0, 0, 0, 10]
              },
              {
                  style: 'mainTable',
                  table: {
                      widths: [60, 75, 130, 140, 80, 105, 100],
                      body: [
                        [{ text: 'Αριθμός Εντολής', style: 'tableHeader', alignment: 'center' },
                         { text: 'Ημερομηνία', style: 'tableHeader', alignment: 'center' },
                         { text: 'Υλικό', style: 'tableHeader' },
                         { text: 'Μεταφορέας', style: 'tableHeader' },
                         { text: 'Αιτούμενες Διελεύσεις', style: 'tableHeader', alignment: 'center' },
                         { text: 'Πραγματοποιημένες Διελεύσεις', style: 'tableHeader', alignment: 'center' },
                         { text: 'Κατάσταση Εκτέλεσης', style: 'tableHeader', alignment: 'center' }
                        ]
                      ]
                  }
              }
            ],
            styles: {
                pageHeader: {
                    bold: false,
                    color: '#000080',
                    fontSize: 8
                },                
                pageFooter: {
                    bold: false,
                    color: '#000080',
                    fontSize: 8
                },
                title: {
                    bold: true,
                    color: '#000080',
                    fontSize: 14
                },
                tableHeader: {
                    bold: true,
                    color: '#000',
                    fontSize: 11
                },
                mainTable: {
                    color: '#000',
                    fontSize: 10
                }
            }
        };

        docDefinition.header.columns[1].text = 'Δήμος: '+this.ds.customerName;
        var arrayLength = data.length;
        for (var i = 0; i < arrayLength; i++) {
            var item = data[i];
            var dt = new Date(item.IssueDate);
            var dtStr = dt.toLocaleDateString('el-GR')
                        ;//+ " " + dt.toLocaleTimeString('el-GR', { hour12: false, hour: 'numeric', minute: 'numeric' });
            
            docDefinition.content[1].table.body.push(
                [
                    {text:item.TransOrderId, style:'', alignment: 'center'}, // 'Αρ. Εντολής',
                    {text: dtStr, style:'', alignment: 'center'}, // 'Ημερομηνία'
                    {text:item.MaterialName.toString(), style:''}, // Υλικό
                    {text:item.TransporterName.toString(), style:''}, // Μεταφορέας
                    {text:this.numberToString(item.NoOfPassages), style:'', alignment: 'center'}, // Αιτούμενες Διελεύσεις
                    {text:this.numberToString(item.NoOfPassagesRecorded), style:'', alignment: 'center'}, // Πραγματοποιημένες Διελεύσεις
                    {text:item.StatusName, style:'', alignment: 'center'} // Κατάσταση Εκτέλεσης
                ]);
            
        }
        pdfMake.createPdf(docDefinition).download();

        return Promise.resolve(true);
    }



    numberToString(i:Number):string {
        if ((i) && (i != null)) {
            return i.toString();
        } 
        else {
            return '0';
        }

    }

    exportToCsv(){
        // fetch all
        this.getAllTransportOrdersView().then(v => {
            // build csv
            this.busyService.busy(this.buildCsv(v));
        });    
    }

    buildCsv(data : Transport_Orders_View[]):Promise<boolean> {
        
        var titles = [
            'Αριθμός Εντολής',
            'Ημερομηνία',
            'Μεταφορέας',
            'Υλικό',
            'Αιτούμενες Διελεύσεις',
            'Πραγματοποιημένες Διελεύσεις',
            'Κατάσταση Εκτέλεσης'
        ];

        // build csv
        //console.log(data);
        var csv = titles.join(';');
        csv += "\n";

        var arrayLength = data.length;
        for (var i = 0; i < arrayLength; i++) {
            var item = data[i];
            var dt = new Date(item.IssueDate);

            var cells =
            [
                item.TransOrderId, // 'Αρ. Εντολής',
                dt.toLocaleDateString('el-GR')
                    + " " + dt.toLocaleTimeString('el-GR', { hour12: false, hour: 'numeric', minute: 'numeric' })
                , // 'Ημερομηνία'
                item.TransporterName.toString(), // Μεταφορέας
                item.MaterialName.toString(), // Υλικό
                this.numberToString(item.NoOfPassages), // Αιτούμενες Διελεύσεις
                this.numberToString(item.NoOfPassagesRecorded), // Πραγματοποιημένες Διελεύσεις
                item.StatusName // Κατάσταση Εκτέλεσης
                ];
            csv += cells.join(';');
            csv += "\n";
        }
        
        var hiddenElement = document.createElement('a');
        hiddenElement.href = 'data:text/csv;charset=utf-8,' + '\uFEFF' + encodeURI(csv);
        hiddenElement.target = '_blank';
        hiddenElement.download = 'Εντολές Μεταφοράς.csv';
        hiddenElement.click();

           return Promise.resolve(true);
         
    }
}



