import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { AuthenticationService } from '../_authentication/index';

interface IDictionary<T> {
    [index: string]: T[];
}

@Component({
    moduleId: module.id,
    templateUrl: 'forgotpassword.component.html'
})

export class ForgotPasswordComponent implements OnInit {
    model: any = {email: null};
    loading = false;
    error = '';

    constructor(
        private router: Router,
        private authService: AuthenticationService,
        public toastr: ToastsManager
        ) { }

    ngOnInit(){
        //
    }

    save() {
        this.loading = true;
        this.authService.forgotPassword(this.model.email)
            .subscribe(
                data => {
                    this.router.navigate(['/']);
                    this.toastr.info('Σας στάλθηκε ηλεκτρονικό μήνυμα. Κοιτάξτε το ταχυδρομείο σας.');
                },
                error => {
                    this.loading = false;
                    try
                    {   
                        console.log(error);
                        if (error.status == 0)
                        {
                           this.toastr.error('No Answer from Server');
                        }
                        else if (error.status == 400)
                        {
                           this.toastr.error('Not Found');
                        }
                        const body = error.json();
                        let modelState: IDictionary<string> = body.ModelState;
                        let errors:string = "";
                        for (var field in modelState) {
                            errors = errors + modelState[field];
                        }
                        this.error = errors;
                    }
                    catch (catcherror)
                    {
                        alert(error);
                    }
                });
    }
}



