import { Location } from '@angular/common';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FileRead, DataService, TransOrdStatuses, NotifyService } from '../_services/index';
import { Transport_Orders, Gate_Trans_View 
       } from '../_entities/entity-model';
import * as FileSaver from "file-saver";
import { SelectItem, LazyLoadEvent } from 'primeng/primeng';
import { FileHelper } from '../_helpers/index';




@Component({
    moduleId: module.id,
    templateUrl: 'gatetrans-of-transporder.component.html',
    styleUrls: ['gatetrans-of-transporder.component.css']

})
export class GatetransOfTrasporderComponent implements OnInit {
    
    title = 'Διελεύσεις Εντολής Μεταφοράς';
    transpOrder : Transport_Orders = new Transport_Orders;
    data : Gate_Trans_View[];

    TransportOrderGUID: string;

    constructor(private route: ActivatedRoute,
        private location: Location,
        private notify: NotifyService,
        private ds: DataService
    ) { }

    ngOnInit() {
        this.route.params
            .subscribe(params => {
                //const TransportOrderGUID = params['id'];
                this.TransportOrderGUID = params['id'];
          
                this.ds.getTransportOrderViewByGUID(this.TransportOrderGUID)
                    .then((v) => {this.transpOrder = v});
                this.ds.getGateTransForTransportOrderGUID(this.TransportOrderGUID)
                    .then((v) => {this.data = v});
                
                return this.TransportOrderGUID;
            });        
        
    }

    goBack() {
        this.location.back(); // <-- go back to previous location on cancel
    }

}
     



