import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../_authentication/index';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { DataService } from '../_services/index';


@Component({
    moduleId: module.id,
    templateUrl: 'home.component.html'
})

export class HomeComponent implements OnInit {
    userName: string = null;
    customerName: string = null;
    dataLoaded: Boolean = false;

    constructor(private authService: AuthenticationService, 
                private notify: ToastsManager,
                public ds: DataService
    ) { }

    ngOnInit() {
        // get users from secure api end point
        // this.userService.getUsers()
        //     .subscribe(users => {
        //         this.users = users;
        //     });        
        this.userName = this.authService.userName;       
        this.ds.getUser_WebUserData_called = false;
        this.ds.getUser_WebUserData().then(value => {
            if (value != undefined)
            {
                this.userName = value.PersonName + " " + value.PersonSurname
                this.customerName = value.CustomerIID.toString();
            }
            this.dataLoaded = true;
        });
    }
}


