import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AuthenticationService } from '../_authentication/index';

@Component({
    moduleId: module.id,
    templateUrl: 'login.component.html'
})

export class LoginComponent implements OnInit {
    model: any = {};
    loading = false;
    error = '';

    constructor(
        private router: Router,
        private authenticationService: AuthenticationService) { }

    ngOnInit() {
        // reset login status
        this.authenticationService.logout();
    }

    login() {
        this.loading = true;
        this.authenticationService.login(this.model.username, this.model.password)
            .subscribe(result => {
                if (result === true) {
                    this.router.navigate(['/']);
                } else {
                    this.error = 'Το όνομα χρήστη ή ο Κωδικός είναι εσφαλμένα';
                    this.loading = false;
                }
            }, error => {
                console.log("error in login > " + error);
                this.error = undefined;
                if (error._body != undefined) {
                    const body = JSON.parse(error._body);    
                    if (body.error_description != undefined) {
                        this.error = body.error_description;    
                    }
                }                
                if (this.error == undefined) {
                    this.error = 'Σφάλμα κατά την επικοινωνία με τον σέρβερ ή το όνομα χρήστη ή ο Κωδικός είναι εσφαλμένα';
                }
                this.loading = false;
            });
    }

    newComplaint() {
       this.router.navigate(['/newapplication']);                
    }
}



