import { UserInfo } from './_authentication/authentication.service';
import { Routes, RouterModule } from '@angular/router';

import { authRoutes, LoginComponent, LoginHelpComponent, RegisterComponent, 
         ChangePasswordComponent, 
         ForgotPasswordComponent, ResetPasswordComponent } from './_authentication/index';
import { HomeComponent } from './home/index';
import { AuthGuard } from './_guards/index';

//import { NewComplaintComponent, ComplaintsComponent, ComplaintComponent, EditComplaintComponent } from './complaints/index';         
import { TransportOrdersListComponent, TransportOrderComponent,
         GatetransOfTrasporderComponent } from './transport-orders/index';         

import { Faq } from './faq/faq.component';
import { Guidelines } from './guidelines/guidelines.component';

import {UserInfoComponent} from './userinfo/userinfo.component';


const appRoutes: Routes = [
    /* Authentication */
    { path: 'login', component: LoginComponent },
    { path: 'loginhelp', component: LoginHelpComponent },
    { path: 'register', component: RegisterComponent },
    { path: 'forgotpassword', component: ForgotPasswordComponent },
    { path: 'resetpassword', component: ResetPasswordComponent },
    { path: 'changepassword', component: ChangePasswordComponent, canActivate: [AuthGuard] },
    /*
    { path: 'newapplication', component: NewComplaintComponent },
    */
    { path: 'listofapplications', component: TransportOrdersListComponent, canActivate: [AuthGuard] },    
    { path: 'application/:id', component: TransportOrderComponent, canActivate: [AuthGuard] },
    { path: 'applicationgatetrans/:id', component: GatetransOfTrasporderComponent, canActivate: [AuthGuard] },
        
    /*
    { path: 'application/:id', component: ComplaintComponent, canActivate: [AuthGuard] },
    { path: 'editapplication/:id', component: EditComplaintComponent, canActivate: [AuthGuard] },
    */

    { path: '', component: HomeComponent, canActivate: [AuthGuard] },

    { path: 'userinfo', component: UserInfoComponent, canActivate: [AuthGuard] },
    { path: 'faq', component: Faq},
    { path: 'guidelines', component: Guidelines},

    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

export const routing = RouterModule.forRoot(appRoutes);


