import { Inject, Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map'
import { APP_CONFIG, IAppConfig, DevAppConfig, DevServicesBaseUrl } from '../app.config';
import { authConfig } from 'app/_authentication/auth.config';
import { environment } from '.../../environments/environment';
import { DOCUMENT } from '@angular/platform-browser';

export class RegisterUserModel {
        UserName : string;
        PersonName : string;
        PersonSurname : string;
        Email : string;
        Password : string;
        ConfirmPassword : string;

        CompanyName : string;
        CompanyPosition : string;                
        Address : string;                
        TelephoneNumber : string;
    }

export class UserInfo {
    EMail: string;
}

export class UserExtendedInfo {
        UserExtId: number;
        PersonName : string;
        PersonSurname : string;
        Email : string;
        Password : string;
        ConfirmPassword : string;

        CompanyName : string;
        CompanyPosition : string;                
        Address : string;                
        TelephoneNumber : string;
}

export class ChangePasswordModel {
    oldPassword: string;
    newPassword: string;
    confirmPassword: string;
}

export class ResetPasswordModel {
    id: string;
    code: string;
    newPassword: string;
    confirmPassword: string;
}

@Injectable()
export class AuthenticationService {

    

    public token: string;
    public userName: string;
    public get isAuthenticated(): boolean { return (this.userName != null)};
    

    constructor(private http: Http, 
                @Inject(APP_CONFIG) private config,
                @Inject(DOCUMENT) private document: any
                ) {
        // set token if saved in local storage localStorage / sessionStorage
        var currentUser = JSON.parse(sessionStorage.getItem(authConfig.lcStorageUserItemStr));
        this.token = currentUser && currentUser.token;
        this.userName = currentUser && currentUser.username;
    }

    login(username: string, password: string): Observable<boolean> {
        var body = 'grant_type=password&username=' + username + '&password=' + password;        
        return this.http.post(this.buildUrl(authConfig.apiLoginUrl), body)
            .map((response: Response) => {
                // login successful if there's a jwt token in the response                
                let token = response.json() && response.json().access_token;

                if (token) {
                    // set token property
                    this.token = token;
                    this.userName = username;                    

                    // store username and jwt token in local storage to keep user logged in between page refreshes
                    sessionStorage.setItem(authConfig.lcStorageUserItemStr, JSON.stringify({ username: username, token: token }));

                    // return true to indicate successful login
                    return true;
                } else {
                    // return false to indicate failed login
                    return false;
                }
            });
    }

    logout(): void {
        // clear token remove user from local storage to log user out
        this.token = null;
        this.userName = null;
        sessionStorage.removeItem(authConfig.lcStorageUserItemStr);
    }

    // Register a new user
    register(regUser: RegisterUserModel): Observable<boolean> {
        let body = JSON.stringify(regUser);
        console.log(body);

        let headers = new Headers({ 'Content-Type': 'application/json; charset=UTF-8' });
        //headers.append('Access-Control-Allow-Origin', '*');
        let options = new RequestOptions({ headers: headers });
     

        return this.http.post(this.buildUrl(authConfig.apiRegisterUrl), body, options)
                    .map((response: Response) => {return true;});

    }

    buildUrl(uri:string): string {
        var res: string;
        
        /*
        if (this.document.location.hostname == 'localhost')
          res = this.config.servicesBaseUrl + uri;
        else
          res = this.document.location.origin +"/PublicApi"+ uri;
        */
        if (!environment.production) {  // Τρέχω μέσα απο το VS CODE
            res = this.config.servicesBaseUrl + uri;
        }
        else
        {
            res = "../WebApi"+ uri;
        }
        console.log("AutService.buildUrl: res="+res+" doc.loc.orig=" + this.document.location.origin);
        return res;
    }

    userInfo(): Observable<UserInfo> {
        /*
        let headers = new Headers({ 'Content-Type': 'application/json; charset=UTF-8' });
        let options = new RequestOptions({ headers: headers });
        */
        let headers = new Headers({ 'Authorization': 'Bearer ' + this.token });
        let options = new RequestOptions({ headers: headers });        
        return this.http.get(this.buildUrl(authConfig.apiUserInfoUrl),options)
        .map(this.extractData)
                    .catch(this.handleError);

    }
    
    private extractData(response: Response) {      
        var user : UserInfo = new UserInfo();
        var jsonFromBody : any = response.json();
        user.EMail = jsonFromBody.Email;
        return user || { };
    }

    userExtendedInfo(): Observable<UserExtendedInfo> {
        /*
        let headers = new Headers({ 'Content-Type': 'application/json; charset=UTF-8' });
        let options = new RequestOptions({ headers: headers });
        */
        let headers = new Headers({ 'Authorization': 'Bearer ' + this.token });
        let options = new RequestOptions({ headers: headers });        
        return this.http.get(this.buildUrl(authConfig.apiUserExtendedInfoUrl), options)
                    .map(this.extractExtendedData)
                    .catch(this.handleError);

    }
    
    private extractExtendedData(response: Response) {      
      var user : UserExtendedInfo = new UserExtendedInfo();
      var jsonFromBody : any = response.json();
      user.UserExtId = jsonFromBody.UserExtId;      
      user.Email = jsonFromBody.Email;      
      user.CompanyName = jsonFromBody.CompanyName;
      user.Address = jsonFromBody.Address;
      user.PersonName = jsonFromBody.PersonName;
      user.PersonSurname = jsonFromBody.PersonSurname;
      user.CompanyPosition = jsonFromBody.CompanyPosition;
      user.TelephoneNumber = jsonFromBody.TelephoneNumber;
      
      return user || { };
   }




    updateUserExtendedInfo(userInfo: UserExtendedInfo): Observable<boolean> {
        
        let headers = new Headers({ 'Authorization': 'Bearer ' + this.token });
        headers.append('Content-Type', 'application/json');
        let options = new RequestOptions({ headers: headers });        
        return this.http.post(this.buildUrl(authConfig.apiUpdateUserExtendedInfoUrl),
                              JSON.stringify(userInfo),
                              options)
                    .map((response: Response) =>{                        
                        return response.ok;
                    })
                    .catch(this.handleError);
    }

    changePassword(model: ChangePasswordModel): Observable<boolean> {
        let headers = new Headers({ 'Authorization': 'Bearer ' + this.token });
        headers.append('Content-Type', 'application/json');
        let options = new RequestOptions({ headers: headers });        
        return this.http.post(this.buildUrl(authConfig.apiChangePasswordUrl),
                              JSON.stringify(model),
                              options)
                    .map((response: Response) =>{                        
                        return response.ok;
                    })
                    .catch(this.handleError);        
    }

    forgotPassword(email: string): Observable<boolean> {
        var body = '?email=' + email;        
        var url:string = this.buildUrl(authConfig.apiForgotPasswordUrl) + body;
        return this.http.post(url, '')
                    .map((response: Response) =>{                        
                        return response.ok;
                    })
                    .catch(this.handleError);        
    }

    resetPassword(model: ResetPasswordModel): Observable<boolean> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });        
        return this.http.post(this.buildUrl(authConfig.apiResetPasswordUrl),
                              JSON.stringify(model),
                              options)
                    .map((response: Response) =>{                        
                        return response.ok;
                    })
                    .catch(this.handleError);        
    }


   private handleError (error: Response | any) {
        // In a real world app, you might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        console.error(errMsg);
        //return Observable.throw(errMsg);
        return Observable.throw(error);
   }
}



