import { Component } from '@angular/core';
import { Router } from '@angular/router';

import { AuthenticationService, RegisterUserModel } from '../_authentication/index';

interface IDictionary<T> {
    [index: string]: T[];
}

@Component({
    moduleId: module.id,
    templateUrl: 'register.component.html'
})

export class RegisterComponent {
    model: RegisterUserModel = new RegisterUserModel();
    loading = false;
    error = '';

    constructor(
        private router: Router,
        private authService: AuthenticationService
        ) { 

        //  this.model = {
        //      UserName: 'andreas4',
        //      PersonName: 'John',
        //      PersonSurname: 'Pappa',
        //      Password: 'Aa3456!',
        //      ConfirmPassword: 'Aa3456!',
        //      Email: 'info4@motion.gr',
        //      CompanyName: 'mill',
        //      CompanyPosition: 'Mngr',
        //      Address: 'sxisto',
        //      TelephoneNumber: '2104567890'        
        //      };
        }

    register() {
        this.loading = true;
        this.authService.register(this.model)
            .subscribe(
                data => {
                    //this.alertService.success('Registration successful', true);
                    this.router.navigate(['/login']);
                },
                error => {
                    this.loading = false;
                    //this.alertService.error(error);
                    //console.log("error: "+error);
                    try
                    {
                        var body = JSON.parse(error._body); // Κάνει το string -> object                   
                        let modelState: IDictionary<string> = body.ModelState;
                        let errors:string = "";
                        for (var field in modelState) {
                            errors = errors + modelState[field];
                        }
                        this.error = errors;
                    }
                    catch (error)
                    {
                        alert(error);
                    }

                    
                });
    }
}



