import { Component } from '@angular/core';
import { Router } from '@angular/router';

import { AuthenticationService, ChangePasswordModel } from '../_authentication/index';

interface IDictionary<T> {
    [index: string]: T[];
}

@Component({
    moduleId: module.id,
    templateUrl: 'changepassword.component.html'
})

export class ChangePasswordComponent {
    model: ChangePasswordModel = new ChangePasswordModel();
    loading = false;
    error = '';

    constructor(
        private router: Router,
        private authService: AuthenticationService
        ) { }

    save() {
        this.loading = true;
        console.log(this.model);
        this.authService.changePassword(this.model)
            .subscribe(
                data => {
                    //this.alertService.success('Registration successful', true);
                    this.router.navigate(['/login']);
                },
                error => {
                    this.loading = false;
                    try
                    {   
                        const body = error.json();
                        let modelState: IDictionary<string> = body.ModelState;
                        let errors:string = "";
                        for (var field in modelState) {
                            errors = errors + modelState[field];
                        }
                        this.error = errors;
                    }
                    catch (catcherror)
                    {
                        alert(error);
                    }
                });
    }
}



