import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

//import { AuthenticationService, UserExtendedInfo } from '../_authentication/index';
//import { AuthenticationService } from '../_authentication/index';
import { DataService } from '../_services/data.service';
import { web_users } from '../_entities/web-users';

interface IDictionary<T> {
    [index: string]: T[];
}

@Component({
    moduleId: module.id,
    templateUrl: 'userinfo.component.html'
})

export class UserInfoComponent implements OnInit {
    //user: UserExtendedInfo = new UserExtendedInfo();
    user: web_users = new web_users;
    //model: any = {};
    loading = false;
    error = '';

    constructor(
        private router: Router,
        //private authService: AuthenticationService,
        public ds: DataService,
        private toastr: ToastsManager
        ) { }

    ngOnInit(){
        this.fetchUserInfo();
    }

    fetchUserInfo(){
        this.ds.getUser_WebUserData_called = false;
        this.ds.getUser_WebUserData().then(value => {
            this.user = value;
        });             
    }

    save() {
        this.loading = true;      
        this.ds.saveChanges()
          .then(value => {
            this.loading = false;
            this.router.navigate(['/']);
            this.toastr.info("Τα στοιχεία χρήστη αποθηκεύτηκαν")
          })
          .catch(reason => {
            this.loading = false;
            this.toastr.error('Error from Server' + reason);
          });
    }
}



