import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import 'rxjs/add/operator/switchMap';

import { AuthenticationService, ResetPasswordModel } from '../_authentication/index';

@Component({
    moduleId: module.id,
    templateUrl: 'resetpassword.component.html'
})

export class ResetPasswordComponent implements OnInit {
    model: ResetPasswordModel = new ResetPasswordModel();
    loading = false;
    error = '';

    constructor(
        private route: ActivatedRoute,        
        private router: Router,
        private authenticationService: AuthenticationService) { 
            
        }

    ngOnInit() {
        // reset login status
        this.authenticationService.logout();
        /*    
        this.model.id = this.route.snapshot.params['id'];
        this.model.code = this.route.snapshot.params['code'];   
        console.log(this.route.snapshot.params); 
        */
        this.route.params
            .subscribe((params: Params) => {
                console.log("On Init");
                console.log(params);
                this.model.id = params['id'];
                this.model.code = params['code'];   
                console.log(this.model);
                return ;
            });

    }

    save() {
        this.loading = true;
        //this.model.password = '';
        //this.model.confirmPassword = '';
        this.authenticationService.resetPassword(this.model)
            .subscribe(result => {
                if (result === true) {
                    this.router.navigate(['/']);
                } else {
                    this.error = 'Password is incorrect';
                    this.loading = false;
                }
            }, error => {
                console.log("error in ResetPassword > " + error)
                this.error = 'Password is incorrect';
                this.loading = false;
            });
    }
}


